body {
  padding: 0;
  min-height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


html, body{
  padding: 0;
  margin: 0;
}
html{
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  height: 100%;
}


.App-header {
  background-color: #252525;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* font-size: calc(10px + 2vmin); */
  font-size: 16px;
  font-family: "helvetica";
  font-weight: 400;
  color: white;
  min-height: 5vh;
  border-bottom-width: 0px;
  border-color: black;
}

.App-body {
  /* background-color: #404040; */
  min-height: 4rem;
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  /* align-items: stretch; */
  justify-content: center;
  /* font-size: calc(10px + 2vmin); */
  font-size: 16px;
  font-family: "helvetica";
  font-weight: 400;
  min-height: 90vh;
  color: white;
}

.App-footer {
  background-color: #252525;
  min-height: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: bottom;
  /* font-size: calc(10px + 2vmin); */
  color: "black";
  font-size: 10px;
  min-height: 5vh;
  font-family: "Helvetica";
}
